.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.App-cell {
  font-size: 1em;
  height: 1.2em;
  width: 1.2em;
  margin: 0;
  padding: 0;
}

td.App-cell:after {
  content: '';
  display: block;
  margin-top: 100%;
}
.active {
  background-color: #3c3c3c;
}

.inactive {
  background-color: #D3D3D3;
}
.App-container {
  margin-left: 5%;
  margin-top:  5%;
  align: center
 
}

.App-link {
  color: #09d3ac;
}
